import React from "react";
import Button from "@material-ui/core/Button";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import { makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";

export default function Publications() {
  const classes = useStyles();
  const history = useHistory();
  return (
    <div className={classes.root}>
      <h5 className={classes.header}>PUBLICATIONS</h5>
      <div className={classes.float}>
        <div>
          <img src="./logo192.png" alt="Deepak k. gupta" />
        </div>
        <div>
          <img src="./logo192.png" alt="Deepak k. gupta" />
        </div>
        <div>
          <img src="./logo192.png" alt="Deepak k. gupta" />
        </div>
      </div>
      <div className={classes.left}>
        <div>
          <h5>
            <a
              className={classes.heading}
              href=" "
              onClick={() => history.push("/TransmuteAI/rescaling")}
            >
              Rescaling CNN through Learnable Repetition of Network Parameters
            </a>
          </h5>
          <div className={classes.width}>
            {/* <div className={classes.root}> */}
            <h6 className={classes.none}>
              <a href=" " onClick={() => history.push("/TransmuteAI/arnav")}>
                Arnav Chavan ,
              </a>
            </h6>
            <h6 className={classes.none}>
              <a href=" " onClick={() => history.push("/TransmuteAI/udbhav")}>
                Udbhav Bamba ,
              </a>
            </h6>
            <h6 className={classes.none}>
              <a href=" " onClick={() => history.push("/TransmuteAI/rishab")}>
                Rishabh Tiwari ,
              </a>
            </h6>
            <h6 className={classes.none}>
              <a href=" " onClick={() => history.push("/TransmuteAI/deepak")}>
                Deepak K. Gupta
              </a>
            </h6>
            {/* </div> */}
          </div>
          <div className={classes.inter}>
            <h6 className={classes.paragraph}>
              International Conference on Image Processing(ICIP)
            </h6>
            <h6 className={classes.paragraph}>May, 2021</h6>
          </div>
          <div>
            <ButtonGroup
              size="small"
              aria-label="small outlined button group"
              color="#002147"
            >
              <Button
                className={classes.link}
                onClick={() => history.push("/TransmuteAI/rescaling")}
              >
                Details
              </Button>
              <Button>
                <a
                  href="https://arxiv.org/abs/2101.05650"
                  target="_blank"
                  rel="noreferrer"
                  className={classes.link}
                >
                  PDF
                </a>
              </Button>
              <Button>
                <a
                  href="https://github.com/transmuteAI/RepeatNet/"
                  target="_blank"
                  rel="noreferrer"
                  className={classes.link}
                >
                  Code
                </a>
              </Button>
            </ButtonGroup>
          </div>
        </div>
        <div>
          <h5>
            <a
              className={classes.heading}
              onClick={() => history.push("/TransmuteAI/generating")}
              href=" "
            >
              Generating Annotated High-Fidelity Images containing Multiple
              Coherent Objects
            </a>
          </h5>
          <div className={classes.width}>
            {/* <div className={classes.root}> */}
            <h6 className={classes.none}>
              <a href=" " onClick={() => history.push("/TransmuteAI/bryan")}>
                Bryan Cardenas ,
              </a>
            </h6>
            <h6 className={classes.none}>
              <a
                href=" "
                onClick={() => history.push("/TransmuteAI/deepanshu")}
              >
                devanshu Arya ,
              </a>
            </h6>
            <h6 className={classes.none}>
              <a href=" " onClick={() => history.push("/TransmuteAI/deepak")}>
                Deepak Kumar Gupta.
              </a>
            </h6>
            {/* </div> */}
          </div>
          <div className={classes.inter}>
            <h6 className={classes.paragraph}>
              International Conference on Image Processing(ICIP){" "}
            </h6>
            <h6 className={classes.paragraph}>May, 2021</h6>
          </div>
          <div>
            <ButtonGroup
              size="small"
              aria-label="small outlined button group"
              color="#002147"
            >
              <Button
                className={classes.link}
                onClick={() => history.push("/generating")}
              >
                Details
              </Button>
              <Button>
                <a
                  href="https://arxiv.org/abs/2006.12150"
                  target="_blank"
                  rel="noreferrer"
                  className={classes.link}
                >
                  PDF
                </a>
              </Button>
              <Button>
                <a
                  href="https://github.com/Cynetics/MSGNet/"
                  target="_blank"
                  rel="noreferrer"
                  className={classes.link}
                >
                  Code
                </a>
              </Button>
            </ButtonGroup>
          </div>
        </div>
        <div>
          <h5>
            <a
              className={classes.heading}
              onClick={() => history.push("/TransmuteAI/chipnets")}
              href=" "
            >
              ChipNet: Budget-Aware Pruning with Heaviside Continuous
              Approximations
            </a>
          </h5>
          <div className={classes.width}>
            {/* <div className={classes.root}> */}
            <h6 className={classes.none}>
              <a href=" " onClick={() => history.push("/TransmuteAI/rishab")}>
                Rishabh Tiwari ,
              </a>
            </h6>
            <h6 className={classes.none}>
              <a href=" " onClick={() => history.push("/TransmuteAI/udbhav")}>
                Udbhav Bamba ,
              </a>
            </h6>
            <h6 className={classes.none}>
              <a href=" " onClick={() => history.push("/TransmuteAI/arnav")}>
                Arnav Chavan ,
              </a>
            </h6>
            <h6 className={classes.none}>
              <a href=" " onClick={() => history.push("/TransmuteAI/deepak")}>
                Deepak K. Gupta
              </a>
            </h6>
            {/* </div> */}
          </div>
          <div className={classes.inter}>
            <h6 className={classes.paragraph}>
              International Conference on Image Processing(ICIP){" "}
            </h6>
            <h6 className={classes.paragraph}>May, 2021</h6>
          </div>
          <div>
            <ButtonGroup
              size="small"
              aria-label="small outlined button group"
              color="#002147"
            >
              <Button
                className={classes.link}
                onClick={() => history.push("/chipnets")}
              >
                Details
              </Button>
              <Button>
                <a
                  href="https://openreview.net/forum?id=xCxXwTzx4L1"
                  target="_blank"
                  rel="noreferrer"
                  className={classes.link}
                >
                  PDF
                </a>
              </Button>
              <Button>
                <a
                  href="https://github.com/transmuteAI/ChipNet"
                  target="_blank"
                  rel="noreferrer"
                  className={classes.link}
                >
                  Code
                </a>
              </Button>
            </ButtonGroup>
          </div>
        </div>
      </div>
    </div>
  );
}

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    padding: "1%",
  },
  header: {
    fontFamily: "Serif",
    color: "#1ca9c9",
    fontSize: "32px",
    [theme.breakpoints.up("sm")]: {
      fontSize: "48px",
      padding: "1%",
      // boxShadow: "0 0 6px #9E9E9E",
    },
  },
  float: {
    display: "none",
    [theme.breakpoints.up("md")]: {
      display: "block",
      width: "40%",
      float: "left",
    },
  },
  left: {
    width: "100%",
    [theme.breakpoints.up("md")]: {
      width: "60%",
      float: "left",
      paddingTop: "2%",
    },
  },
  heading: {
    fontFamily: "Serif",
    color: "#1ca9c9",
    // boxShadow: "0 0 6px #9E9E9E",
  },
  width: {
    width: "100%",
    float: "none",
    paddingBottom: "1%",
  },
  none: {
    float: "left",
    marginTop: "-9px",
    fontSize: "14px",
    [theme.breakpoints.up("md")]: {
      fontSize: "16px",
      marginTop: "-11px",
    },
  },
  inter: {
    width: "100%",
    marginTop: "5%",
    // boxShadow: "0 0 6px #9E9E9E",
    [theme.breakpoints.up("md")]: {
      marginTop: "1%",
      width: "100%",
    },
  },
  button: {
    marginBottom: "1%",
  },
  link: {
    color: "black",
  },
  bottommargin: {
    marginBottom: "3%",
  },
  paragraph: {
    fontFamily: "Sans-serif",
    fontSize: "14px",
    [theme.breakpoints.up("md")]: {
      fontSize: "16px",
    },
  },
}));
